"use client"

import React, { useState, useEffect, useRef } from 'react';
import { 
  DollarSign, 
  History, 
  X, 
  Plus, 
  Minus, 
  PieChart, 
  CreditCard, 
  Send,
  Wallet,
} from 'lucide-react';

const FinanceAssistant = () => {
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([
    { sender: 'assistant', text: '💰 Welcome to your Personal Finance Assistant! How can I help you today?' }
  ]);
  const [loading, setLoading] = useState(false);
  const [dots, setDots] = useState(1);
  const [sessionId] = useState(() => `session_${Date.now()}`);
  const chatEndRef = useRef(null);

  useEffect(() => {
    chatEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages, loading]);

  // Animate typing dots
  useEffect(() => {
    if (!loading) return;
    const interval = setInterval(() => {
      setDots(d => (d % 3) + 1);
    }, 500);
    return () => clearInterval(interval);
  }, [loading]);

  const sendMessage = async (e) => {
    e?.preventDefault();
    if (!message.trim()) return;
    
    const userMessage = { sender: 'user', text: message };
    const newMessages = [...messages, userMessage];
    setMessages([...newMessages, { sender: 'assistant', text: 'Assistant is typing', typing: true }]);
    setMessage('');
    setLoading(true);
    
    try {
      const res = await fetch('https://dddemo.net:4000/chat', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ 
          message: message, 
          sessionId: sessionId 
        })
      });
      
      const data = await res.json();
      
      // Add assistant response to messages
      setMessages([...newMessages, { sender: 'assistant', text: data.response }]);
      
    } catch (err) {
      setMessages([...newMessages, { sender: 'assistant', text: '❌ Error connecting to backend.' }]);
    }
    
    setLoading(false);
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const clearHistory = async () => {
    try {
      // Clear history on backend
      await fetch('http://localhost:5000/clear-history', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ sessionId: sessionId })
      });
      
      // Reset frontend messages
      setMessages([
        { sender: 'assistant', text: '💰 Welcome to your Personal Finance Assistant! How can I help you today?' }
      ]);
      
    } catch (err) {
      console.error('Error clearing history:', err);
      // Still clear frontend even if backend fails
      setMessages([
        { sender: 'assistant', text: '💰 Welcome to your Personal Finance Assistant! How can I help you today?' }
      ]);
    }
  };

  const getHistory = async () => {
    try {
      const res = await fetch('http://localhost:5000/get-history', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ sessionId: sessionId })
      });
      
      const data = await res.json();
      console.log('Conversation History:', data.history);
      
      // Show history in a simple alert (you can make this prettier)
      if (data.history && data.history.length > 0) {
        const historyText = data.history
          .map(msg => `${msg.role}: ${msg.content}`)
          .join('\n\n');
        alert(`Conversation History:\n\n${historyText}`);
      } else {
        alert('No conversation history found.');
      }
      
    } catch (err) {
      console.error('Error getting history:', err);
      alert('Error fetching history.');
    }
  };

  const handleQuickAction = (action) => {
    let actionMessage = '';
    switch (action) {
      case 'balance':
        actionMessage = "What's my balance?";
        break;
      case 'categories':
        actionMessage = "Show my expenses by category";
        break;
      case 'transactions':
        actionMessage = "Show recent transactions";
        break;
      case 'expense':
        actionMessage = "I spent 500 on groceries";
        break;
      case 'income':
        actionMessage = "I earned 50000 from salary";
        break;
      default:
        actionMessage = "How can I help you with your finances today?";
    }
    setMessage(actionMessage);
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-6xl mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <DollarSign className="w-8 h-8 text-amber-500" />
              <h1 className="text-2xl font-semibold text-gray-800">Finance Assistant</h1>
            </div>
            <div className="flex space-x-3">
              <button 
                onClick={getHistory}
                className="flex items-center space-x-2 px-4 py-2 bg-teal-500 text-white rounded-lg hover:bg-teal-600 transition-colors"
              >
                <History className="w-4 h-4" />
                <span>View History</span>
              </button>
              <button 
                onClick={clearHistory}
                className="flex items-center space-x-2 px-4 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors"
              >
                <X className="w-4 h-4" />
                <span>Clear History</span>
              </button>
            </div>
          </div>
          <div className="flex justify-between items-center mt-2 text-sm text-gray-600">
            <span>Session ID: {sessionId}</span>
            <span>{messages.filter(msg => msg.sender === 'user').length} messages sent</span>
          </div>
        </div>
      </div>

      <div className="max-w-6xl mx-auto px-4 py-6">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Chat Interface */}
          <div className="lg:col-span-3">
            <div className="bg-white rounded-xl shadow-sm border h-[80vh] flex flex-col">
              {/* Chat History */}
              <div className="flex-1 overflow-y-auto p-4 bg-gray-50">
                {messages.map((msg, idx) => (
                  <div 
                    key={idx} 
                    className={`mb-4 flex ${msg.sender === 'user' ? 'justify-end' : 'justify-start'}`}
                  >
                    <div className={`max-w-[70%] px-4 py-3 rounded-2xl ${
                      msg.sender === 'user' 
                        ? 'bg-blue-500 text-white' 
                        : 'bg-white border border-gray-200 text-gray-800'
                    } shadow-sm`}>
                      {msg.typing ? (
                        <div className="flex items-center space-x-2">
                          <DollarSign className="w-4 h-4 text-amber-500" />
                          <span className="text-gray-500 italic">
                            Assistant is typing{'.'.repeat(dots)}
                          </span>
                        </div>
                      ) : (
                        <div className={`${msg.sender === 'assistant' ? 'flex items-start space-x-2' : ''}`}>
                          {msg.sender === 'assistant' && (
                            <DollarSign className="w-4 h-4 text-amber-500 mt-1 flex-shrink-0" />
                          )}
                          <span className="whitespace-pre-wrap">{msg.text}</span>
                        </div>
                      )}
                    </div>
                  </div>
                ))}
                <div ref={chatEndRef} />
              </div>

              {/* Message Input */}
              <div className="p-4 border-t bg-white">
                <div className="flex space-x-3">
                  <input
                    type="text"
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                    onKeyPress={handleKeyPress}
                    placeholder="Type your message..."
                    disabled={loading}
                    className={`flex-1 px-4 py-3 border rounded-full focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                      loading ? 'bg-gray-100' : 'bg-white'
                    }`}
                  />
                  <button
                    onClick={sendMessage}
                    disabled={loading || !message.trim()}
                    className={`px-6 py-3 rounded-full font-medium flex items-center space-x-2 transition-colors ${
                      loading || !message.trim()
                        ? 'bg-gray-400 cursor-not-allowed'
                        : 'bg-blue-500 hover:bg-blue-600 cursor-pointer'
                    } text-white`}
                  >
                    <Send className="w-4 h-4" />
                    <span>Send</span>
                  </button>
                </div>
              </div>
            </div>

            {/* Quick Action Buttons */}
            <div className="mt-6 flex flex-wrap gap-3">
              <button
                onClick={() => handleQuickAction('balance')}
                className="flex items-center space-x-2 px-4 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors"
              >
                <Wallet className="w-4 h-4" />
                <span>Check Balance</span>
              </button>
              <button
                onClick={() => handleQuickAction('categories')}
                className="flex items-center space-x-2 px-4 py-2 bg-orange-500 text-white rounded-lg hover:bg-orange-600 transition-colors"
              >
                <PieChart className="w-4 h-4" />
                <span>View Categories</span>
              </button>
              <button
                onClick={() => handleQuickAction('transactions')}
                className="flex items-center space-x-2 px-4 py-2 bg-teal-600 text-white rounded-lg hover:bg-teal-700 transition-colors"
              >
                <CreditCard className="w-4 h-4" />
                <span>Recent Transactions</span>
              </button>
              <button
                onClick={() => handleQuickAction('expense')}
                className="flex items-center space-x-2 px-4 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors"
              >
                <Minus className="w-4 h-4" />
                <span>Add Expense</span>
              </button>
              <button
                onClick={() => handleQuickAction('income')}
                className="flex items-center space-x-2 px-4 py-2 bg-emerald-500 text-white rounded-lg hover:bg-emerald-600 transition-colors"
              >
                <Plus className="w-4 h-4" />
                <span>Add Income</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FinanceAssistant;